import React from "react"
import Cell from "../components/cell"

import fire from "../components/images/fire.png"
import win from "../components/images/win.png"
import normal from "../components/images/normal.png"
import lose from "../components/images/lose.png"

export default class Board extends React.Component {
  constructor(props) {
    super(props)
  }
  state = {
    boardData: this.initBoardData(
      this.props.height,
      this.props.width,
      this.props.mines
    ),
    gameWon: false,
    gameLost: false,
    mineCount: this.props.mines,
    isFinal: false,
  }

  /* Helper Functions */

  // get mines
  getMines(data) {
    let mineArray = []

    data.map(datarow => {
      datarow.map(dataitem => {
        if (dataitem.isMine) {
          mineArray.push(dataitem)
        }
      })
    })

    return mineArray
  }

  // get Flags
  getFlags(data) {
    let mineArray = []

    data.map(datarow => {
      datarow.map(dataitem => {
        if (dataitem.isFlagged) {
          mineArray.push(dataitem)
        }
      })
    })

    return mineArray
  }

  // get Hidden cells
  getHidden(data) {
    let mineArray = []

    data.map(datarow => {
      datarow.map(dataitem => {
        if (!dataitem.isRevealed) {
          mineArray.push(dataitem)
        }
      })
    })

    return mineArray
  }

  // get random number given a dimension
  getRandomNumber(dimension) {
    // return Math.floor(Math.random() * dimension);
    return Math.floor(Math.random() * 1000 + 1) % dimension
  }

  // Gets initial board data
  initBoardData(height, width, mines) {
    let data = []

    for (let i = 0; i < height; i++) {
      data.push([])
      for (let j = 0; j < width; j++) {
        data[i][j] = {
          x: i,
          y: j,
          isMine: false,
          neighbour: 0,
          isRevealed: false,
          isEmpty: false,
          isFlagged: false,
        }
      }
    }
    data = this.plantMines(data, height, width, mines)
    data = this.getNeighbours(data, height, width)
    console.log(data)
    return data
  }

  // plant mines on the board
  plantMines(data, height, width, mines) {
    let randomx,
      randomy,
      minesPlaced = 0

    while (minesPlaced < mines) {
      randomx = this.getRandomNumber(width)
      randomy = this.getRandomNumber(height)
      if (!data[randomx][randomy].isMine) {
        data[randomx][randomy].isMine = true
        minesPlaced++
      }
    }

    return data
  }

  // get number of neighbouring mines for each board cell
  getNeighbours(data, height, width) {
    let updatedData = data,
      index = 0

    for (let i = 0; i < height; i++) {
      for (let j = 0; j < width; j++) {
        if (data[i][j].isMine !== true) {
          let mine = 0
          const area = this.traverseBoard(
            data[i][j].x,
            data[i][j].y,
            data,
            height,
            width
          )
          area.map(value => {
            if (value.isMine) {
              mine++
            }
          })
          if (mine === 0) {
            updatedData[i][j].isEmpty = true
          }
          updatedData[i][j].neighbour = mine
        }
      }
    }

    return updatedData
  }

  // looks for neighbouring cells and returns them
  traverseBoard(x, y, data, height, width) {
    const el = []

    //up
    if (x > 0) {
      el.push(data[x - 1][y])
    }

    //down
    if (x < height - 1) {
      el.push(data[x + 1][y])
    }

    //left
    if (y > 0) {
      el.push(data[x][y - 1])
    }

    //right
    if (y < width - 1) {
      el.push(data[x][y + 1])
    }

    // top left
    if (x > 0 && y > 0) {
      el.push(data[x - 1][y - 1])
    }

    // top right
    if (x > 0 && y < width - 1) {
      el.push(data[x - 1][y + 1])
    }

    // bottom right
    if (x < height - 1 && y < width - 1) {
      el.push(data[x + 1][y + 1])
    }

    // bottom left
    if (x < height - 1 && y > 0) {
      el.push(data[x + 1][y - 1])
    }

    return el
  }

  // reveals the whole board
  revealBoard() {
    let updatedData = this.state.boardData
    updatedData.map(datarow => {
      datarow.map(dataitem => {
        dataitem.isRevealed = true
      })
    })
    this.setState({
      boardData: updatedData,
    })
  }

  /* reveal logic for empty cell */
  revealEmpty(x, y, data) {
    let area = this.traverseBoard(x, y, data)
    area.map(value => {
      if (!value.isRevealed && (value.isEmpty || !value.isMine)) {
        data[value.x][value.y].isRevealed = true
        if (value.isEmpty) {
          this.revealEmpty(value.x, value.y, data)
        }
      }
    })
    return data
  }

  // Handle User Events

  handleCellClick(x, y) {
    let win = false

    // check if revealed. return if true.
    if (this.state.boardData[x][y].isRevealed) return null

    // check if mine. game over if true
    if (this.state.boardData[x][y].isMine) {
      this.state.gameLost = true
      this.revealBoard()
      this.state.boardData[x][y].isFinal = true
      alert("game over")
    }

    let updatedData = this.state.boardData
    updatedData[x][y].isFlagged = false
    updatedData[x][y].isRevealed = true

    if (updatedData[x][y].isEmpty) {
      updatedData = this.revealEmpty(x, y, updatedData)
    }

    if (this.getHidden(updatedData).length === this.props.mines) {
      win = true
      this.revealBoard()
      alert("You Win")
    }

    this.setState({
      boardData: updatedData,
      mineCount: this.props.mines - this.getFlags(updatedData).length,
      gameWon: win,
    })
  }

  _handleContextMenu(e, x, y) {
    e.preventDefault()
    let updatedData = this.state.boardData
    let mines = this.state.mineCount
    let win = false

    // check if already revealed
    if (updatedData[x][y].isRevealed) return

    if (updatedData[x][y].isFlagged) {
      updatedData[x][y].isFlagged = false
      mines++
    } else {
      updatedData[x][y].isFlagged = true
      mines--
    }

    if (mines === 0) {
      const mineArray = this.getMines(updatedData)
      const FlagArray = this.getFlags(updatedData)
      win = JSON.stringify(mineArray) === JSON.stringify(FlagArray)
      if (win) {
        this.revealBoard()
        alert("You Win")
      }
    }

    this.setState({
      boardData: updatedData,
      mineCount: mines,
      gameWon: win,
    })
  }

  renderBoard(data) {
    return data.map(datarow => {
      return datarow.map(dataitem => {
        return (
          <div key={dataitem.x * datarow.length + dataitem.y}>
            <Cell
              onClick={() => this.handleCellClick(dataitem.x, dataitem.y)}
              cMenu={e => this._handleContextMenu(e, dataitem.x, dataitem.y)}
              value={dataitem}
            />
            {datarow[datarow.length - 1] === dataitem ? (
              <div className="clear" />
            ) : (
              ""
            )}
          </div>
        )
      })
    })
  }
  // Component methods
  componentWillReceiveProps(nextProps) {
    if (JSON.stringify(this.props) !== JSON.stringify(nextProps)) {
      this.setState({
        boardData: this.initBoardData(
          nextProps.height,
          nextProps.width,
          nextProps.mines
        ),
        gameWon: false,
        mineCount: nextProps.mines,
      })
    }
  }

  restartGame = lost => {
    this.setState({
      boardData: this.initBoardData(
        this.props.height,
        this.props.width,
        this.props.mines
      ),
      gameWon: false,
      gameLost: false,
      mineCount: this.props.mines,
      isFinal: false,
    })
  }

  render() {
    return (
      <div className="board">
        <div className="game-stats">
          <div
            className={["info-parent mb-1", "gameState"].join(" ")}
            onClick={() => this.restartGame(this.state.gameLost)}
          >
            <div className="">
              <span className="game-emoji">
                {this.state.gameWon ? (
                  <div className="endstate">
                    <img src={fire} />
                    <img src={win} />
                    YOU WIN
                    <img src={win} />
                    <img src={fire} />
                  </div>
                ) : this.state.gameLost ? (
                  <div className="endstate">
                    <img src={lose} />
                    GAME OVER
                    <img src={lose} />
                  </div>
                ) : (
                  <img src={normal} />
                )}
              </span>
            </div>
          </div>
        </div>
        {this.renderBoard(this.state.boardData)}
        <div className="game-stats mt-1">
          <div className="info-parent">
            <div className="info">
              {this.state.mineCount}
              <span className="info-title">mines</span>
            </div>
            <div className="info">
              {this.props.height}X{this.props.width}
              <span className="info-title">grid</span>
            </div>
            <div className="info">
              {this.props.difficulty}
              <span className="info-title">difficulty</span>
            </div>
          </div>
        </div>
      </div>
    )
  }
}
